.App {
  text-align: center;
}

.bookshelf {
  width: 50%; /* Adjust this value to get the desired size */
  margin: 0 auto;
  display: block;
}

.button-container {
  position: relative;
  z-index: 1; /* Ensures buttons appear above the bookshelf */
  margin-top: 20px; /* Adjust as necessary */
}

button {
  padding: 10px 20px;
  font-size: 16px;
  margin: 5px;
  cursor: pointer;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
